<template>
  <div class="hello">
    <div class="container">
      <div class="row">
        <div class="col-md-8 col-md-offset-2">
          <h1>YakStak
            <small><span class="sign-out">(<a href="#" @click.prevent="signOut" style="font-family: Quicksand">Sign Out</a>)</span></small>
          </h1>
          <h2 class="user-info">
            <small>
              {{ user.name() ? user.name() : 'Your'}} Yaks
            </small>
            <small class="pull-right">
            {{ user.username ? user.username : user.identityAddress }}
            </small>
          </h2>
          <form @submit.prevent="addYak(yaktxt)" :disabled="! yaktxt">
            <div class="input-group">
              <input v-model="yaktxt" type="text" class="form-control" placeholder="Write a yak..." autofocus>
              <span class="input-group-btn">
                <button class="btn btn-default" type="submit" :disabled="! yaktxt">Yak</button>
              </span>
            </div>
          </form>

          <ul class="list-group">
                <yaks v-for="yak in yakdata" :yak="yak"></yaks>
          </ul>

          <!-- <small id="demo"></small> -->

        </div>
      </div>
    </div>
  </div>
</template>

<!-- ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ -->

<script>
import Yaks from './Yaks.vue'

var STORAGE_FILE = 'yaks.json'

export default {
  name: 'dashboard',
  components: {Yaks},
  props: ['user'],
  data () {
    return {
      yakdata: [],
      yaktxt: '',
      votes: 0,
      uidCount: 0,
      blockstack: window.blockstack
    }
  },
  watch: {
    yakdata: {
      handler: function (yakdata) {
        const blockstack = this.blockstack
        // const encrypt = true
        let options = {
          public: true
        }
        return blockstack.putFile(STORAGE_FILE, JSON.stringify(yakdata), options)
      },
      deep: true
    }
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    addYak (yaktxt) {
      this.yakdata.unshift({
        id: this.uidCount++,
        post: yaktxt,
        votes: 0,
        upvoted: false,
        downvoted: false
      })
      this.yaktxt = ''
    },

    // FIX THIS DATA STORAGE ???
    fetchData () {
      const blockstack = this.blockstack
      let options = {
        user: 'a2.id',
        app: 'http://localhost:8080'
      }
      blockstack.getFile(STORAGE_FILE, options)
      // const decrypt = true
      // blockstack.getFile(STORAGE_FILE, decrypt)
      .then((yakData) => {
        var parsedYaks = JSON.parse(yakData || '[]')
        console.log(parsedYaks)
        parsedYaks.forEach(function (yak, index) {
          yak.id = index
        })
        this.uidCount = parsedYaks.length
        this.yakdata = parsedYaks
      })
    },

    lookupPerson () {
      document.getElementById('demo').innerHTML = this.blockstack.lookupProfile('m1.id', 'http://localhost:6270/v1/names/')
    },

    signOut () {
      this.blockstack.signUserOut(window.location.href)
    }
  }
}
</script>

<!-- ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ -->
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

@import url('https://fonts.googleapis.com/css?family=Megrim');
@import url('https://fonts.googleapis.com/css?family=Kaushan+Script');
@import url('https://fonts.googleapis.com/css?family=Oxygen');
@import url('https://fonts.googleapis.com/css?family=Cabin');
@import url('https://fonts.googleapis.com/css?family=Quicksand');

h1 {
    color: #40e0d0;
    font-size: 3em;
    font-family: 'Megrim', sans-serif;
}

h2 {
    color: #f7f7f7;
    font-size: 3em;
    font-family: 'Quicksand', sans-serif;
}

h3 {
    color: white;
}

button {
  background-color: #40e0d0;
  border: none;

  &:hover {
    background-color: lighten(#40e0d0, 15%);
  }

  &:active {
    background-color: lighten(#40e0d0, 20%);
  }
}

input::placeholder {
    color: #696969;
    font-family: 'Cabin', sans-serif;
    font-weight: lighter;
}

input {
  color: #696969;
  font-family: 'Cabin', sans-serif;
  font-weight: lighter;
}


.list-group-item {
  &.completed label {
    text-decoration: line-through;
  }

  .delete {
    display: none;
  }

  &:hover .delete {
    display: inline;
    color: grey;
    &:hover {
      text-decoration: none;
      color: red;
    }
  }
}

a {
  padding-left: 5px;
}

</style>



